import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { environment } from 'src/environments/environment';
import { Category } from 'src/app/models/category.model';
import { Brand } from 'src/app/models/brand.model';

@Component({
  selector: 'app-product-form',
  templateUrl: './product-form.component.html',
  styleUrls: ['./product-form.component.scss']
})
export class ProductFormComponent extends ResourceFormContent {
  title: string;
  message: string;
  formGroup: FormGroup;

  brands: Brand[];
  categories: Category[];

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    http.get<any[]>(environment.prepareUrl('brandsFetchUrl')).toPromise().then(res => this.brands = res);
    http.get<any[]>(environment.prepareUrl('categoriesFetchUrl')).toPromise().then(res => this.categories = res);

    this.formGroup = new FormGroup({
      name: new FormControl(null, Validators.required),
      brand_id: new FormControl(null, Validators.required),
      category_id: new FormControl(null, Validators.required),
      price: new FormControl(null, Validators.required),
      is_main: new FormControl(false, Validators.required),
    });
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static'
    };

    return this.service.open(this.content, options);
  }

  postUrl(): string {
    return 'productsCreateUrl';
  }
  putUrl(): string {
    return 'productsUpdateUrl';
  }

  initData(): void {
    super.initData();

    if (history.state.resource)
    {
      this.formGroup.patchValue({
        price: history.state.resource.price.value
      });
    }
  }
}

